import { PointUtils } from "./Point";
export class PaintingKeyframe {
    constructor(point) {
        this.point = point;
        this.speed = 500;
    }
    ;
    flatten() {
        return {
            point: PointUtils.flatten(this.point),
            speed: this.speed
        };
    }
    static restore(data) {
        const p = new PaintingKeyframe(PointUtils.restore(data.point));
        p.speed = data.speed;
        return p;
    }
    apply(path) {
        path.effectivePointsPerSeconds = this.speed;
    }
}
